import React from 'react';
import Post from '../components/posts/Post';
import PostImage from '../img/PostImage.png'

const Posts = () => {
    var card = {
        status: "Мироприятие",
        picture: PostImage,
        name: "Название",
        tags: [["онлайн", "#333"], ["JavaScript", "#444"]],
        description: "lorem Impusim"
    }

    return (
        <div className='posts'>
            <Post status={card.status} 
            picture={card.picture}
            name={card.name} 
            tags={card.tags} 
            description={card.description} />
        </div>
    );
}

export default Posts;
