import React from 'react';
import ViewPost from './ViewPost';

const Post = props => {
  var statusStyle = {backgroundColor: "green"}
  var tagStyle = {}

  return (
    <>
      <div className='card'>
        <div className='cardPrew'>
          <div className='status' style={statusStyle}>{props.status}</div>
          <img src={props.picture} alt="" />
        </div>
        <div className='cardContent'>
          <div className='cardText'>
            <h5>{props.name}</h5>
            <div className='tags'>
              {
                props.tags.map(e=>{
                tagStyle = {backgroundColor: e[1]}
                return <div className='tag' style={tagStyle}>{e[0]}</div>
              })
              }
            </div>
            <p>{props.description}</p>
            <div className='cardButtons'>
              <button>Я приду!</button>
              <button>Подробнее</button>
              {/* <ViewPost /> */}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Post;
